.navleiste{

padding: 0px;

text-align: center;
background-color: rgb(218, 218, 214);
}

.navleiste ul{
  list-style: none;
  display: flex;
  justify-content: end;
  padding-right: 20px;
  margin:0;
}
.navleiste ul li{
  padding-left: 20px;
  font-size: 2.5rem;
}
.link{
  text-decoration: none;
  color:aliceblue
}
.home{
  text-align: center;
}

.product{

  padding-top: 2vh;
  display: flex;
  justify-content: center;
}

.ueberschrift{
  font-size: 3rem;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
  color: lime;
  text-shadow: 4px 4px 8px black;
  text-align: center;
}

.prod{
  font-size: 3rem;
  padding: 20px;
}

.zeitenWrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
}
.tag{
  display: flex;
  align-items:center ;
  justify-content: center;
  font-size: 2rem;
  margin: 10px;
}
